.widget{
    position: sticky;
    top: 80px;
    flex: 0.2;
    border: 1px solid lightgray;
    height: fit-content;
    border-radius: 5px;
    background-color: white;
    padding-bottom: 10px;
}
.widget_header{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
} 
.widget_header > h2{
    font-size: 16px;
    font-weight: 400;
}
.widget_articleLeft{
    margin-right: 5px ;
    color: #0177b7; 
}

.widget_article{
    display: flex;
    padding: 10px;
    cursor: pointer;
   
    

}
.widget_article:hover{
    background-color: whitesmoke;
   
    

}
.widget_articleRight{
   flex: 1; 
}
.widget_articleRight > h4{
    font-size: 14px;
}
.widget_articleRight > p{
    color: gray;
    font-size: 12px;
    font-weight: 400;
}
.widget_articleLeftIcon{
    width: 15px !important;
}