.login{
    display: grid;
    place-items: center;
    padding-top: 100px;
    padding-bottom: 100px;
    margin-left: auto;
    margin-right: auto;
}

.login > img{
   object-fit: contain;
   height: 70px;
   margin-top: 20px;
   margin-bottom: 20px;
}

.login > form {
    display: flex;
    flex-direction: column;
}
.login > form > input{
    width: 350px;
    height: 50px;
    font-size: 20px;
    padding-left: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
}
.login > form > button {
    width: 365px;
    height: 50px;
    font-size: 20px;
    border-radius: 5px;
    color: white;
    background-color: #0074b1
}
.login > p{
    margin-top: 20px;
}
span{
    color: #0074b1;
    cursor: pointer;
}